#root {
  padding: 25px;
}
.logo {
  height: 20vw;
  margin-bottom: 20px;
}
.links {
  -webkit-transition: -webkit-transform 0.4s;
  transition: transform 0.4s;
}

.links:hover {
  -webkit-transform: scale(1.4) rotate(0.01deg);
  transform: scale(1.4) rotate(0.01deg);
}

@keyframes move {
  100% {
    transform: translate3d(0, 0, 1px) rotate(360deg);
  }
}

.background {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background: #13071c;
  overflow: scroll;
}

.background span {
  width: 1vmin;
  height: 1vmin;
  border-radius: 1vmin;
  backface-visibility: hidden;
  position: absolute;
  animation: move;
  animation-duration: 45;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.background span:nth-child(0) {
  color: #ffacac;
  top: 74%;
  left: 56%;
  animation-duration: 7s;
  animation-delay: -42s;
  transform-origin: -23vw 7vh;
  box-shadow: 2vmin 0 0.726953014411055vmin currentColor;
}
